import { useEffect } from "react";
import { motion } from "framer-motion";
import Tooltip from '@mui/material/Tooltip';
export default function UpdateCancelBtn({
  handleReset,
  handleUpdate,
  disableUpdate = false,
  updateBtnColor = "btn-green",
}) {
  useEffect(() => {
    const handleKeyDown = (e) => {
      e.stopPropagation();
      if (e.altKey) {
        switch (e.key.toLowerCase()) {
          case "u":
            e.preventDefault();
            e.stopPropagation();
            document.querySelector("#btnUpdate").click()
            break;
          default:
            break;
        }
      }
    };
    document.addEventListener("keydown", handleKeyDown);
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  }, []);
  return (
    <div className="btnMainContainer saveCancel col-md-10 col-sm-10 form-group print_box_bg low_margin">
      <div>
        <motion.button
          type="button"
          className="btn-style blue-btn"
          onClick={handleReset}
          whileTap={{
            scale: "0.8",
          }}
        >
          Cancel
        </motion.button>
        {disableUpdate ? (
          <>
            <Tooltip title="Update loaded data" placement="top">
              <motion.button
              type="button"
              className={`btn-style btn-disable`}
              onClick={handleUpdate}
              whileTap={{
                scale: "0.8",
              }}
            >
              Update
            </motion.button>  
            </Tooltip>
          </>
        ) : (
          <>
          <Tooltip title="Update loaded data" placement="top">
            <motion.button
              type="button"
              className={`btn-style ${updateBtnColor}`}
              onClick={handleUpdate}
              whileTap={{
                scale: "0.8",
              }}
              id="btnUpdate"
            >
              Update
            </motion.button>
            </Tooltip>
          </>
        )}
      </div>
    </div>
  );
}
